.nav {
    display: flex;
    width: 300px;
    margin-left: auto;
}
.nav-item {
    text-decoration: none;
    margin: 20px;
    color: rgb(47, 46, 46);

    &:hover {
        color: rgb(56, 53, 53);
        text-decoration: underline;
    }
}
.CardOneNew {
    border-radius: 10px;
    color: $text3;
    p{

        padding-top: 5px;
      text-align: justify;
    }

}
.CardOneNew-img {
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    img {
        border-radius: 10px;
        width: 200px;
        height: 100px;
    }
}
.CardOneNew-content{
    height:  220px;
    overflow-y: auto;
h3{
    font-size: 16px;
}
    @include mobail{
        font-size: 13px;
        //   height:  120px;
    overflow-y: auto;
    }

}
.CardOneNew-content-item{
    margin-bottom: 15px;
    padding-right: 10px;
}
.dataCardNews{
    background-color: #fff;
    margin-top: 20px;
    width: 320px;
    height: 340px;
padding: 10px;
